import { ref } from 'vue';
import { fromCode } from '@/utils/validate';
import jjdx from "@/assets/jjdx.png";
export default {
  setup() {
    const XnxqOption = ref("");
    const dialogVisible = ref(false);

    const handleClose = done => {
      ElMessageBox.confirm('确定关闭对话框?', '温馨提示', {
        type: 'info',
        center: true
      }).then(() => {
        done();
      });
    };

    return {
      XnxqOption,
      dialogVisible,
      handleClose
    };
  },

  data() {
    return {
      // 分页
      currentPage: 1,
      pagesize: 10,
      sections: ["周次\\节", "1-2节", "3-4节", "5-6节", "7-8节", "9-10节"],
      userInfo: null,
      XnxqList: [],
      Xnxq: {},
      search: "",
      // 选课的结果
      XkList: [],
      xnxqh: "",
      //
      logo: jjdx
    };
  },

  created() {
    const user = sessionStorage.getItem("userInfo");

    if (user) {
      this.userInfo = JSON.parse(fromCode(user)); // 根据请求获取用户信息
    } else {
      this.$router.push("/login");
    }

    this.getXnxqList();
  },

  methods: {
    // 分页
    handleSizeChange: function (size) {
      this.pagesize = size;
    },
    handleCurrentChange: function (currentPage) {
      this.currentPage = currentPage;
    },

    // 获取 学期学年列表
    getXnxqList() {
      this.$axios.get("/weixin-ggjxzl/getXnxqList").then(res => {
        const data = res.data;

        if (data.code == 200) {
          this.XnxqList = data.data;
        } else {
          ElMessage.error("获取学年数据错误");
        }
      });
    },

    // 根据 学期， 教师id 获取 实验室课表
    getXkListByXnxqAndJgId(xnxq, JgId) {
      this.$axios.post("/weixin-sysxk/Jgpklist", {
        "Xnxq": xnxq,
        "JgId": JgId
      }).then(res => {
        const data = res.data;
        this.xnxqh = xnxq;

        if (data.code == 200) {
          this.XkList = data.data;

          if (!this.XkList.length) {
            ElMessage.success("查询无数据");
          }
        }
      });
    },

    // 选择学年
    changeXnxq() {
      this.Xnxq = JSON.parse(JSON.stringify(this.XnxqOption));
      this.XnxqOption = this.Xnxq.qsrq;
    },

    QuerySchedule() {
      // 判断 是否 选择了学期
      if (this.XnxqOption) {
        if (this.xnxqh != this.Xnxq.xnxqh) {
          Loading.show();
          this.getXkListByXnxqAndJgId(this.Xnxq.xnxqh, this.userInfo.useraccount);
          Loading.hide();
        }
      } else {
        ElMessage.error("请选择查询的学期");
      }
    },

    ExportSchedule() {
      // 判断 是否有数据
      if (this.XnxqOption) {
        if (this.XkList.length) {
          this.dialogVisible = true;
        } else {
          ElMessage.error("无数据导出，点击查询试试");
        }
      } else {
        ElMessage.error("请先选择学期，点击查询");
      }
    },

    ExportPdf() {
      this.$pdf.getPdf('pdfDom', "实验室课程表" + this.getdate().replace(/\s/g, ""));
    },

    ExportPdf2() {
      this.$pdf.getPdf('pdfDom2', "123");
    },

    getdate() {
      var date = new Date();
      var seperator1 = "-";
      var year = date.getFullYear();
      var month = date.getMonth() + 1;
      var strDate = date.getDate();

      if (month >= 1 && month <= 9) {
        month = "0" + month;
      }

      if (strDate >= 0 && strDate <= 9) {
        strDate = "0" + strDate;
      }

      var currentdate = year + " 年 " + month + " 月 " + strDate + " 日 ";
      return currentdate;
    }

  },
  computed: {
    getXkFilter() {
      return this.XkList.map(t => {
        t['address'] = t.yxsmc + t.sysmph + "教室";
        t['time'] = "第" + t.kkzc + "周,周" + t.kkDay + " 、" + this.sections[t.kkSection];
        t['bj'] = t.bjmcList.join('、');
        return t;
      }).filter(data => !this.search || data.kcmc.includes(this.search.toLowerCase()) || data.yxsmc.includes(this.search.toLowerCase()) || data.sysmph.includes(this.search.toLowerCase()) || data.jgmc.includes(this.search.toLowerCase()));
    }

  }
};